import { urlPrefix } from './settings.js';


export class eventManager {
    constructor() {
        this.subscriptions = []
        this.showMessage = () => {}

        this.listen()
    }

    onMessage(event) {
        try {
            let data = JSON.parse(event.data)
            this.handleEvent(data)
        } catch (error) {
            this.showMessage("Event Source Parsing", "Could not parse message. " + error)
        }
    }

    onError() {
        // reconnect on error
        setTimeout(() => {
            console.log("Lost connection to EventSource. Reconnecting...")
            this.source.close()
            this.source = null
            this.listen(true)
        }, 3000)
    }

    onReconnect() {
        console.log("Reconnected to EventSource")
        this.handleEvent({message: "reconnected to server"})
    }

    listen (reconnect=false) {
        this.source = new EventSource(urlPrefix + "/events")
        this.source.onmessage = this.onMessage.bind(this)
        this.source.onerror = this.onError.bind(this)
        if (reconnect) this.source.onopen = this.onReconnect.bind(this)
    }


    handleEvent(event) {
        let showMessage = this.showMessage
        this.subscriptions.forEach(obj => {
            try {
                obj.eventHandler(event)
            }
            catch (error) {
                showMessage("Event Handler Failed", "Event handler threw the following exception: " + error)
            }
        })
    }


    subscribe(obj) {
        this.subscriptions.push(obj)
    }


    unsubscribe(obj) {
        let idx = this.subscriptions.indexOf(obj)
        delete this.subscriptions[idx]
    }
}