import React, {useState} from 'react';
import {
    ApplyCreateCloseButtons,
    daysToSeconds,
    FormGroup,
    FormGroupSelect,
    makeSubmitHandler,
} from "./formHelpers";

const initialState = {
    "type": "DebugAction",
    "cooldown": 5 * 60,
    "name": "",
    "url": "",
    "method": "get",
    "retain_for": 90 * daysToSeconds};


export function ActionForm({editMode=false, state=initialState, setState, handleClose=()=>{},
                               setActive=()=>{}, stateManager}) {

    let [sending, setSending] = useState(false)

    const handleSubmit = makeSubmitHandler(
        editMode, handleClose, state, setState, initialState, setActive, stateManager)
    const stateProps = {state: state, setState: setState}

    return (
        <form onSubmit={handleSubmit} className={"row"}>
            <div className={"col-lg-4"} style={{ borderRight: "1px solid #dee2e6" }}>
                <h4>Type</h4>
                <FormGroupSelect
                    label={" "}
                    name={"type"}
                    disabled={editMode}
                    {...stateProps}
                >
                    <option value={"DebugAction"}>DebugAction - Debug print statement</option>
                    <option value={"PushoverAction"}>Pushover - Sends a push notification</option>
                    <option value={"WebhookAction"}>Webhook - Makes a webrequest</option>
                </FormGroupSelect>
            </div>

            <div className={"col-lg-4"} style={{ borderRight: "1px solid #dee2e6" }}>
                <h4>Info</h4>
                <FormGroup
                    label={"Name:"}
                    name={"name"}
                    {...stateProps}/>
                <FormGroup
                    label={"Cooldown:"}
                    name={"cooldown"}
                    type={"number"}
                    suffix={"minutes"}
                    factor={60}
                    {...stateProps}/>
                <FormGroup
                    label={"Retain for:"}
                    name={"retain_for"}
                    type={"number"}
                    suffix={"days"}
                    factor={daysToSeconds}
                    {...stateProps}/>
            </div>

            <div className={"col-lg-4"}>
                <h4>Type Specific Info</h4>
                { state.type === "PushoverAction" && ([
                    <FormGroup label={"Api token:"} name={"api_token"} key={"api_token"} {...stateProps}/>,
                    <FormGroup label={"User key:"} name={"user_key"} key={"user_key"} {...stateProps}/>,
                    <FormGroup label={"Device:"} name={"device"} key={"device"} {...stateProps}/>,
                ])}
                { state.type === "WebhookAction" && ([
                    <FormGroup label={"Url:"} name={"url"} key={"url"} {...stateProps}/>,
                    <FormGroupSelect label={"Method:"} name={"method"} key={"method"} {...stateProps}>
                        <option value={"get"}>get</option>
                        <option value={"post"}>post</option>
                        <option value={"put"}>put</option>
                        <option value={"delete"}>delete</option>
                    </FormGroupSelect>,
                ])}
            </div>

            <ApplyCreateCloseButtons editMode={editMode} handleClose={handleClose} label={"Action"}>
                {state.id != null && stateManager != null && <button
                    type={"button"}
                    className={"btn btn-secondary mt-2 ml-2 px-4"}
                    onClick={() => {
                        setSending(true)
                        stateManager.send(state.id, `${state.name}: Test message :)`)
                            .then(() => {setSending(false)})
                            .catch(() => {setSending(false)})
                    }}>
                    { sending && <i className={"spinner-border spinner-border-sm mr-2"}/>}
                    Send Test Notification
                </button>}
            </ApplyCreateCloseButtons>
        </form>
    )
}


