import {stateManager} from "./state";
import {SensorFrom} from "./SensorForms";
import {TriggerFrom} from "./TriggerForms";
import {ActionForm} from "./ActionForms";
import React, {useState} from "react";
import {DetailView} from "./DetailView";
import {eventManager} from "./events";
import {ListView} from "./ListView";
import {Sensor} from "./Sensor";
import {Trigger} from "./Trigger";
import {Action} from "./Action";
import {MessageContainer} from "./Messages";

function Sidebar(props) {
    return (
        <div className={"sidebar bg-night shadow-sm"}>
            <ul className={"list-group list-group-flush"}>
                {props.children}
            </ul>
        </div>
    )
}


function ContentView({FormView, stateManager, children, ...props}) {
    function setFormState(state) {
        stateManager.setState({
            formState: {...stateManager.state().formState, ...state}
        })
    }

    return ([
        <div className={"header h2 text-dark my-auto px-3"} key={"header"}>
            {stateManager.item().name || "Create New"}
        </div>,
        stateManager.state().active === "form" ?
            <div className={"main px-3"} key={"FormView"}>
                <FormView
                    state={stateManager.state().formState}
                    setState={setFormState}
                    setActive={active => {
                        stateManager.setState({active: active})
                    }}
                    stateManager={stateManager}
                    {...props}
                />
            </div>
            :
            <DetailView
                key={"DetailView"}
                stateManager={stateManager}
                FormView={FormView}
                {...props}>
                {children}
            </DetailView>

    ])
}

// create event listener globally
let events = new eventManager()
let itemInfo = {
    "sensors": {
        manager: new stateManager({
            eventManager: events,
            itemLabel: "Sensor",
            eventHandlerItemPrefix: "sensor",
            fetchUrlPath: "/sensors",
        }),
        form: SensorFrom,
        moreInfo: Sensor,
    },
    "triggers": {
        manager: new stateManager({
            eventManager: events,
            itemLabel: "Trigger",
            eventHandlerItemPrefix: "trigger",
            fetchUrlPath: "/triggers",
        }),
        form: TriggerFrom,
        moreInfo: Trigger,
    },
    "actions": {
        manager: new stateManager({
            eventManager: events,
            itemLabel: "Action",
            eventHandlerItemPrefix: "action",
            fetchUrlPath: "/actions",
        }),
        form: ActionForm,
        moreInfo: Action,
    },
}

export function App() {
    let [state, _setState] = useState({messages: []})

    function setState(newState) {
        _setState({...state, ...newState})
    }

    Object.keys(itemInfo).map(key => {
        itemInfo[key].manager.globalState = state
        itemInfo[key].manager.setGlobalState = setState
    })

    let [active, setActive] = useState("sensors")

    function makeTabs() {
        let icons = {
            "sensors": <span className={`fas fa-ruler-vertical mr-2`}/>,
            "triggers": <span className={`fas fa-calculator mr-2`}/>,
            "actions": <span className={`fas fa-bullseye mr-2`}/>,
        }
        return Object.keys(itemInfo).map(key => {
            return (
                <button type="button"
                        className={`btn btn-outline-transparent text-white px-1 text-capitalize 
                        ${active === key ? "active" : ""}`}
                        key={key}
                        onClick={() => {
                            setActive(key)
                        }}>
                    {icons[key]}<br/>{key}
                </button>
            )
        })
    }

    let manager = itemInfo[active].manager
    let MoreInfoView = itemInfo[active].moreInfo

    return (
        <div>
            <div className={"main-grid"}>
                <Sidebar>
                    <div className="btn-group w-100 px-1 py-2">
                        {makeTabs()}
                    </div>
                    <ListView stateManager={manager}/>
                </Sidebar>

                <ContentView
                    stateManager={manager}
                    FormView={itemInfo[active].form}
                    sensorManager={itemInfo.sensors.manager}
                    actionManager={itemInfo.actions.manager}>
                    <MoreInfoView stateManager={manager}/>
                </ContentView>
            </div>

            <MessageContainer
                messages={state.messages}
                setMessages={(newMessages) => setState({messages: newMessages})}/>
        </div>
    )
}