import React from 'react';

export function Message({title, children, className="", handleClose, ...props}) {
    return (
        <div className={"toast show " + className} style={{minWidth: "180px"}}>
            <div className="toast-header">
                <strong className="mr-auto">{title}</strong>
                <button type="button" className="ml-2 mb-1 close" onClick={handleClose}>
                    <span>&times;</span>
                </button>
            </div>
            <div className="toast-body">
                {children}
            </div>
        </div>
    )
}


export function DismissAllButton({handleClose=() => {}}) {
    return (
        <button type="button" className={"btn float-right text-muted"} onClick={handleClose}>
            <div className="close ml-2">
                <span>&times;</span>
            </div>
            <small className={"text-small"}>
                dismiss all
            </small>
        </button>
    )
}

export function MessageContainer({messages, setMessages}) {
    if (messages == null) return null;
    return (
        <div className={"message-container"}>
            {messages.map((message, idx) => {
                return (
                    <Message
                        handleClose={() => {
                            setMessages(messages.filter((message, i) => {
                                return i !== idx
                            }))
                        }}
                        title={message.title}
                        key={idx}>
                        {message.children}
                    </Message>
                )
            })}
            {messages.length > 0 && <DismissAllButton handleClose={() => setMessages([])}/>}
        </div>
    )
}