import React from "react";

export const daysToSeconds = 24 * 60 * 60

export function onChangeHandler(state, setState) {
    return function onChange(e) {
        e.preventDefault()
        let target = e.nativeEvent.target
        let newState = {...state}
        let value = target.value
        if (target.dataset.factor && value != null) {
            value *= target.dataset.factor
        }
        newState[target.name] = value
        setState(newState)
    }
}

export function FormGroupSelect({children, className, name="", label="", state, setState, ...props}) {
    return (
        <div className="form-group">
            {label !== "" && <label>{label}</label>}
            <select
                className={"form-control " + className}
                onChange={onChangeHandler(state, setState)}
                value={state[name]}
                name={name}
                {...props}
            >
                {children}
            </select>
        </div>
    )
}

export function FormGroup(props) {
    return (
        <div className="form-group">
            {"label" in props ? <label>{props.label || "label"}</label> : null}
            <div className="input-group">
                <input type={props.type || "text"}
                       className="form-control"
                       placeholder={props.placeholder || ""}
                       onChange={onChangeHandler(props.state, props.setState)}
                       value={
                           props.factor != null ?
                               props.state[props.name] / props.factor :
                               props.state[props.name]
                       }
                       list={props.list || ""}
                       name={props.name}
                       disabled={props.disabled}
                       data-factor={props.factor}
                />
                {"suffix" in props ? (
                    <div className="input-group-append">
                        <span className="input-group-text">{props.suffix}</span>
                    </div>
                ) : ""}
            </div>
        </div>
    )
}


export function ApplyCreateCloseButtons({handleClose=()=>{}, editMode=false, label="", children}) {
    return (
        <div className={"col-12"}>
            <button type={"submit"} className={"btn btn-primary mt-2 px-4"}>
                {editMode ? "Apply" : `Create ${label}`}
            </button>
            {editMode ?
            <button type={"button"} className={"btn btn-secondary mt-2 ml-2 px-4"} onClick={handleClose}>
                Cancel
            </button>
            : ""}
            {children}
        </div>
    )
}


export function makeSubmitHandler(editMode, handleClose, state, setState, initialState, setActive, stateManager) {

    return function handleSubmit(event) {
        event.preventDefault()

        if (editMode) {
            // edit item
            stateManager.editItem(state)
        } else {
            // new item
            stateManager.addItem(state)
                .then(data => {
                    if (data == null) return;
                    setState(initialState)
                    setActive(data.id)
                })
        }

        handleClose()
    }
}


export function randomReadableString(length=5) {
    function randomChar(string) {
        return string.charAt(Math.floor(Math.random() * string.length))
    }
    let vowels = 'aeoui'
    let consonants = 'qwrtzipsdfghjklyxcvbnm'
    let result = ''
    for (let i = 0; i < length; i++) {
        result += i % 2 === 0 ? randomChar(consonants) : randomChar(vowels)
    }
    return result
}